<template>
    <div class="detail">
        <div class="box">
            <div class="content">
                <div class="title">
                    <span class="left"></span>
                    <p></p>
                    <span class="right"></span>
                </div>
                <div class="boxTwo" v-html="route.routeInfo">
                        <!-- <p class="xingcheng">--</p>
                        <p class="contOne"></p>
                        <p class="xingcheng"></p>
                        <p class="cont"></p>
                        <p class="cont"></p>
                        <p class="cont"></p>
                        <p class="cont"></p> -->
                </div>
            </div>
            <div class="boxImg"></div>
        </div>
    </div>
</template>
<script>
export default {
    props:["route"],
    data(){
        return{

        }
    }
}
</script>
<style scoped>
.detail{
    margin-top:10px;
    width:100%;
    float:left;
    background:#FFFFFF;
    margin-bottom:50px;
}
.box{
    width:100%;
    padding:0 10px;
    float:left;
}
.content{
    padding-top:30px;
    width:100%;
    height:300px;
    /* border:1px solid red; */
}
.left{
    display:block;
    width:20px;
    height:1px;
    background:gray;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    margin-right:15px;
}
.right{
    display:block;
    width:20px;
    height:1px;
    background:gray;
    float:left;
    position:relative;
    top:50%;
    transform:translateY(-50%);
    margin-left:15px;
}
.title{
    /* width:100%; */
    
    height:30px;
    line-height:30px;
    /* height:100px; */
    /* border:1px solid yellow; */
    float:left;
    position:relative;
    left:50%;
    transform:translateX(-50%);
}
.title>p{
    float:left;
    font-size:18px;
    color:#333333;
}
.boxTwo{
    padding-top:30px;
    width:100%;
    float:left;
    
}
.boxTwo >>>p{
    width:100%;
    float:left;
}
.boxTwo >>>p img{
    width:100%;
    float:left;
}
.xingcheng{
    height:30px;
    line-height:30px;
    /* border:1px solid red; */
    font-size:16px;
    color:#333333;
}
.contOne{
     font-size:14px;
    line-height:25px;
    width:100%;
}
.cont{
    font-size:14px;
    line-height:16px;
    width:100%;
    
}
img{
    width:100%;

}
</style>


