export function formatDate(secs, type = 0) { //typejsonyear-month-date,XXXXXX
  var t = new Date(secs)
  var year = t.getFullYear()
  var month = t.getMonth() + 1
  if (month < 10) {
    month = '0' + month
  }
  var date = t.getDate()
  if (date < 10) {
    date = '0' + date
  }
  var hour = t.getHours()
  if (hour < 10) {
    hour = '0' + hour
  }
  var minute = t.getMinutes()
  if (minute < 10) {
    minute = '0' + minute
  }
  var second = t.getSeconds()
  if (second < 10) {
    second = '0' + second
  }
  if (type == 0) {
    return year + '' + month + '' + date + '' + hour + '' + minute + ''
  } else {
    return year + '-' + month + '-' + date
  }
}



